.review_row {
  gap: 20px;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  padding: 0 20px;
}
.testcard {
  background-color: transparent;
  padding: 20px;
  border-radius: 10px;
  box-shadow: 0 4px 8px var(--shadow-color);
}
.testcard i {
  font-size: 20px;
}
.testcard h3 {
  margin-top: 15px;
}
