.hero {
  height: calc(100vh - 70px);
  position: relative;
  overflow: hidden;
}
.hero::after {
  content: "";
  position: absolute;
  width: 200%;
  height: 100%;
  background: linear-gradient(rgba(15, 20, 154, 0.2), rgba(15, 20, 154, 0.4)),
    url("../../../assets/images/bg.jpg") no-repeat;
  background-size: cover;
  background-position: center;
  top: 0;
  left: 0;
  filter: blur(5px);
  z-index: -1;
  animation: scroll 30s linear infinite;
}
@keyframes scroll {
  0% {
    transform: translateX(0);
  }
  100% {
    transform: translateX(-50%);
  }
}
.hero_container {
  max-width: 1200px;
  width: 100%;
  margin: auto;
  z-index: 20;
}
.hero_row {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 30px;
  padding-top: 70px;
}
.hero_col_2 {
  flex-basis: 50%;
  min-width: 275px;
}
.hero_col_2:first-of-type {
  padding: 10px;
}

.hero_col_2 .typed {
  font-size: 50px;
  margin-bottom: 30px;
  transition: all 0.3s;
}
.hero_col_2 p {
  margin-bottom: 30px;
  color: #fff;
  font-size: 18px;
  font-weight: 400;
  transition: all 0.3s;
}
.abt_btn {
  background-color: var(--primary_color);
  color: var(--light-color);
  margin-right: 20px;
}
.abt_btn:hover {
  background-color: var(--hover-color);
}
.ctn_btn {
  background-color: #fff;
  color: var(--primary_color);
}
.ctn_btn:hover {
  background-color: var(--secondary_color);
  color: #fff;
}
.hero_col_2 img {
  width: 100%;
  transition: 0.5s;
  animation: bounce 10s linear infinite;
}
@keyframes bounce {
  0% {
    transform: translateY(0);
  }
  40% {
    transform: translateY(-30px);
  }
  60% {
    transform: translateY(-15px);
  }
  100% {
    transform: translateY(0);
  }
}
@media (max-width: 700px) {
  .hero_col_2 {
    flex-basis: 100%;
  }
  .left_coln {
    text-align: center;
    padding: 20px;
    margin-bottom: 30px;
  }
  .hero {
    height: auto;
  }
  .hero_row {
    padding-top: 10px;
    flex-wrap: wrap;
  }
  .hero_col_2 h1 {
    font-size: 30px;
    line-break: 20px;
    margin-bottom: 15px;
  }
  .hero_col_2 a {
    display: block;
    margin: 0 auto 10px;
  }
  .hero_col_2 img {
    padding: 0px;
  }
}
