/* -------------- Contact section ----------*/
.contact_container {
  width: 100%;
  max-width: 1200px;
  margin: auto;
  padding: 1rem;
}
.contact_title {
  margin-bottom: 30px;
}
.row {
  margin: 2rem 0;
  gap: 1rem;
}
.row .col_2 {
  flex-basis: 50%;
}
.row .col_2:last-of-type {
  padding-inline: 20px;
}
.row .col_2 h3 {
  margin-bottom: 0.5rem;
}

.row .col_2 ul li {
  display: flex;
  align-items: center;
  gap: 10px;
  margin-top: 10px;
}
.row .col_2 img {
  width: 30px;
}
.row .col_2 .social {
  display: flex;
  align-items: center;
  gap: 5px;
  margin-top: 1rem;
}
.row .col_2 .social a {
  width: 50px;
  height: 35px;
  text-decoration: none;
  background-color: transparent;
  border-radius: 10px;
  padding: 15px;
  display: flex;
  align-items: center;
  justify-content: center;
  border: 2px solid var(--primary_color);
  cursor: pointer;
  position: relative;
  overflow: hidden;
  outline: none;
}
.row .col_2 .social a i {
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: var(--primary_color);
  transform: translateY(35px);
  transition: all 0.5s ease-in-out;
}
.row .col_2 .social a:hover i {
  transform: translateY(0px);
}
.row .col_2 .social a img {
  width: 100%;
  z-index: 1;
  transition: all 0.5s;
}
.row .col_2 .social a:hover img {
  transform: rotate(360deg);
}
.contact_map {
  width: 80%;
  margin: 0 auto 30px;
}
.contact_map iframe {
  border: 0;
}
@media (max-width: 767px) {
  .title-wrapper p {
    text-align: center;
  }
  .row {
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
  }
  .row .col_2 {
    width: 100%;
  }
  .row .col_2:last-of-type {
    padding-inline: 0px;
  }
  .contact_map iframe {
    height: 300px;
  }
}
