.typedText {
  color: var(--secondary_color);
  font-size: 50px;
  margin-bottom: 30px;
  font-family: "Poppins-Bold";
}

.blinking-cursor {
  display: inline-block;
  vertical-align: bottom;
  width: 0.2em;
  height: 1.2em;
  margin-left: 0.1em;
  background-color: var(--light-color);
  animation: blink 1s infinite;
}

@keyframes blink {
  0% {
    opacity: 0;
  }
  50% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}
