.letsGetStarted {
  background-color: var(--primary_color);
  color: #fff;
}
.letsGetStarted_title {
  font-size: 1.8rem;
  text-align: center;
}
.letsGetStarted p {
  font-size: 18px;
  font-weight: 400;
  text-align: center;
}
.letsGetStarted span {
  font-size: 16px;
  font-weight: 400;
  text-align: center;
  margin-bottom: 20px;
}
.letsGetStarted a {
  border: 3px solid var(--light-color) !important;
  color: var(--light-color);
}
.letsGetStarted a:hover {
  background-color: #fff;
  color: var(--primary_color);
}
@media (max-width: 767px) {
  .letsGetStarted_container {
    padding: 0 20px;
  }
  .letsGetStarted_title {
    font-size: 1.5rem;
  }
}
