header {
  height: 70px;
  position: sticky;
  left: 0;
  top: 0;
  backdrop-filter: blur(10px);
  z-index: 10;
  box-shadow: 0px 1px 0px 1px var(--primary_color);
}
.header_container {
  width: 100%;
  max-width: 1200px;
  margin: auto;
  padding: 10px;
  transition: 0.3s;
}
.header_row {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.header_row .logo {
  font-size: 1.8em;
  font-weight: bold;
  font-family: "Poppins-bold";
  color: var(--primary_color);
  cursor: pointer;
}
.header_row .logo:hover {
  color: var(--secondary_color);
}
.header_row .nav_wrapper {
  display: flex;
  align-items: center;
}
.menu_toggle {
  padding: 7px;
  cursor: pointer;
  width: 40px;
  border-radius: 5px;
  display: none;
  background-color: var(--primary_color);
  align-items: center;
}
.menu_toggle span {
  height: 3px;
  width: 100%;
  background-color: #fff;
  display: block;
  margin-bottom: 5px;
  transition: all 0.3s ease-in-out;
}
.menu_toggle span:nth-child(1),
.menu_toggle span:nth-child(3) {
  width: 75%;
}
.menu_toggle span:nth-child(3) {
  margin-bottom: 0%;
}

.menu_toggle.active span {
  width: 100%;
}
.menu_toggle.active span:nth-child(2) {
  opacity: 0;
}
.menu_toggle.active span:nth-child(1) {
  transform: translateY(8px) rotate(-45deg);
}
.menu_toggle.active span:nth-child(3) {
  transform: translateY(-8px) rotate(45deg);
}
.header_row .nav_wrapper .nav_elements ul {
  display: flex;
  list-style-type: none;
}
.header_row .nav_wrapper .nav_elements ul li {
  padding: 10px 15px;
}
.header_row .nav_wrapper .nav_elements ul li a {
  font-size: 18px;
  font-weight: bold;
  text-decoration: none;
  transition: all 0.3s;
  color: var(--primary_color);
}
#contact {
  padding: 10px 30px;
  background-color: var(--primary_color);
  color: #fff;
  border-radius: 30px;
  margin-left: 30px;
  font-size: 16px;
  font-weight: bold;
  transition: all 0.3s;
}
#contact:hover {
  background-color: var(--secondary_color);
  border: none;
  color: #fff;
}
.header_row .nav_wrapper .nav_elements ul li a.active {
  color: #fd9b4d;
  position: relative;
}
.header_row .nav_wrapper .nav_elements ul li a:hover {
  color: #fd9b4d;
}
.header_row .nav_wrapper .nav_elements ul li a.active::after {
  content: "";
  width: 100%;
  height: 2px;
  position: absolute;
  bottom: -5px;
  left: 0;
  transition: transform 0.3s;
  transform-origin: left;
  background-color: #fd9b4d;
}
@media (max-width: 700px) {
  .menu_toggle {
    display: block;
  }
  .header_row .logo {
    font-size: 1.5em;
  }
  .header_row .nav_wrapper .nav_elements {
    position: absolute;
    top: 60px;
    left: 0;
    right: 0;
    width: 100%;
    height: 0px;
    opacity: 0;
    background-color: rgb(15, 20, 154, 0.6);
    backdrop-filter: blur(20px);
    display: flex;
    justify-content: center;
    align-items: center;
    transition: all 0.5s ease-in-out;
  }
  .header_row .nav_wrapper .nav_elements.active {
    opacity: 1;
    height: 350px;
  }
  .header_row .nav_wrapper .nav_elements ul {
    flex-direction: column;
    align-items: center;
  }
  .header_row .nav_wrapper .nav_elements ul li {
    display: block;
    margin-bottom: 10px;
  }
  .header_row .nav_wrapper .nav_elements ul li a {
    color: #fff;
  }
  #contact {
    padding: 8px 12px;
  }
}
