#page-loader {
  position: fixed;
  top: 0;
  left: 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100vh;
  width: 100%;
  background: var(--primary_color);
  z-index: 9999;
}

.team-loader {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 30px;
}

#loader-text {
  font-size: 2em;
  text-transform: uppercase;
  color: var(--light-color);
}

.pulse-loader {
  display: flex;
  justify-content: center;
  align-items: center;
  border: 3px solid var(--secondary_color);
  border-radius: 50%;
  height: 100px;
  width: 100px;
  animation: pulsate 1s ease-out infinite;
  opacity: 0;
}

.countup {
  font-size: 2em;
  color: var(--light-color);
}

@keyframes pulsate {
  0% {
    transform: scale(0.1, 0.1);
    opacity: 0;
  }
  50% {
    opacity: 1;
  }
  100% {
    transform: scale(1.3, 1.3);
    opacity: 0;
  }
}
