.contact-row {
  justify-content: space-evenly;
  align-items: center;
  gap: 30px;
}
.order {
  color: var(--dark-color);
}
.order h2 {
  font-size: 1.8rem;
  margin-bottom: 1.5rem;
}
.order p {
  font-size: 18px;
  margin-bottom: 25px;
}
.order a {
  border: 2px solid var(--dark-color);
  color: var(--dark-color);
}
.order a:hover {
  background-color: var(--primary_color);
  color: var(--light-color);
}
.contact_col_2 img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
@media (max-width: 783px) {
  .order {
    text-align: center;
    padding: 20px;
  }
}
