.footer {
  background-color: var(--primary_color);
  color: var(--light-color);
  padding-bottom: 10px !important;
}
.footer h3 {
  margin-bottom: 20px;
  letter-spacing: 1px;
  position: relative;
}

.footer h3::after {
  content: "";
  position: absolute;
  bottom: -5px;
  left: 0;
  height: 4px;
  width: 60px;
  background-color: var(--secondary_color);
  border-radius: 10px;
}
.footer-row {
  padding: 0 15px;
}
.company-footer-sect {
  flex-basis: 50%;
}
.company-footer-sect p {
  margin-bottom: 15px;
}
.social-accts {
  display: flex;
  column-gap: 10px;
  align-items: center;
}
.social-acct-link {
  background-color: var(--light-color);
  transition: all 0.3s;
  width: 30px;
  height: 30px;
  border-radius: 8px;
  padding: 5px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.social-acct-link:hover .icon {
  transform: scale(1.1) rotate(360deg);
}
.social-acct-link .icon {
  font-size: 20px;
  transition: all 0.5s;
}
.footer-links {
  flex-basis: 15%;
}

.footer-link span {
  color: var(--secondary_color);
  font-size: 18px;
  margin-right: 10px;
}
.footer-link:hover a {
  color: var(--secondary_color);
  margin-left: 5px;
}
.footer-links a {
  color: var(--light-color);
  transition: all 0.3s;
}
.footer-contacts {
  flex-basis: 35%;
}
.footer-contacts li {
  display: flex;
  align-items: center;
  gap: 15px;
  margin-bottom: 10px;
}
.footer-contacts .icon {
  font-size: 20px;
  color: var(--light-color);
}
.footer-contacts li p {
  letter-spacing: 1px;
}
.footer hr {
  background-color: var(--secondary_color);
  height: 2px;
  width: 100%;
  margin-bottom: 5px;
}
@media (max-width: 767px) {
  .footer-row {
    padding: 0 25px;
  }
  .footer-coln {
    flex-basis: 100%;
  }
}
