.services_row {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(350px, 1fr));
  gap: 25px;
}
.service_card {
  box-shadow: 0 4px 8px var(--shadow-color);
  border-radius: 8px;
  color: var(--primary_color);
  transition: all 0.3s;
}
.service_card:hover {
  background-color: var(--primary_color);
  color: var(--light-color);
}
.service_img {
  height: 150px;
  text-align: center;
  border-bottom: 1px solid var(--shadow-color);
}
.icon {
  font-size: 20px;
  transition: all 0.5s ease-in-out;
}
.service_card:hover .icon {
  transform: scale(1.2);
}
.service_content {
  padding: 15px 10px;
  text-align: center;
}
.service_content h4 {
  margin-bottom: 10px;
  font-family: "Poppins-bold";
  letter-spacing: 1px;
}
.service_content p {
  margin-bottom: 15px;
}
@media only screen and (max-width: 998px) {
  .services_row {
    padding: 0 25px;
  }
}
