@font-face {
  font-family: Poppins;
  src: url("./assets/fonts/Poppins-Regular.ttf");
}
@font-face {
  font-family: Poppins-bold;
  src: url("./assets/fonts/Poppins-Bold.ttf");
}
@font-face {
  font-family: Poppins-medium;
  src: url("./assets/fonts/Poppins-Medium.ttf");
}
:root {
  --light-white: #f2fbfc;
  --light-color: #ffffff;
  --medium-color: #b9bab3;
  --dark-color: #001f26;
  --shadow-color: rgba(15, 20, 154, 0.3);
  --hover-color: #0910db;
  --primary_color: #0f149a;
  --secondary_color: #fd9d4e;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Poppins";
}

html {
  scroll-behavior: smooth;
}
body {
  color: var(--dark-color);
}
a,
Link {
  text-decoration: none;
}
ul {
  list-style-type: none;
}
.section {
  padding: 80px 0;
  position: relative;
}

.container {
  margin: auto;
  display: flex;
  max-width: 1200px;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.row {
  display: flex;
}

.title {
  text-align: center;
  margin-bottom: 30px;
  font-size: 4em;
  color: var(--primary_color);
  font-family: "Poppins-bold";
}

.btn {
  padding: 10px 1rem;
  border-radius: 5px;
  font-size: 18px;
  font-family: "Poppins-medium";
  transition: all 0.3s;
}

/*-------animation-----*/
.left {
  animation: left 2s linear;
}
@keyframes left {
  0% {
    transform: translateX(-50px);
    opacity: 0;
  }
  100% {
    transform: translateX(0);
    opacity: 1;
  }
}

.top {
  animation: top 2s linear;
}
@keyframes top {
  0% {
    transform: translateY(-50px);
    opacity: 0;
  }
  100% {
    transform: translateY(0);
    opacity: 1;
  }
}
@media only screen and (min-width: 768px) and (max-width: 991px) {
  .section {
    padding-top: 60px;
    padding-bottom: 60px;
  }
  .title {
    font-size: 30px;
  }
  .container {
    width: 450px;
  }
}

@media (max-width: 767px) {
  .section {
    padding-top: 50px;
    padding-bottom: 50px;
  }
  .title {
    font-size: 30px;
    font-family: "Poppins-medium";
  }
}
