.scroll-to-top {
  position: fixed;
  bottom: 20px;
  right: 20px;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 40px;
  height: 40px;
  border-radius: 7px;
  border: 2px solid var(--primary_color);
  outline: none;
  cursor: pointer;
  background-color: transparent;
  color: var(--primary_color);
  font-size: 20px;
  font-weight: bold;
  z-index: 999;
  transition: all 0.5s ease-in-out;
}

.scroll-to-top:hover {
  background-color: var(--primary_color);
  color: var(--light-color);
}
.scroll-to-top:hover svg {
  transform: scale(1.2);
}
