.share_btn {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  position: fixed;
  bottom: 70px;
  right: 20px;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: 0.3s;
}
.share_btn:hover {
  transform: scale(1.1);
}
.share_btn a {
  text-decoration: none;
  display: flex;
  align-items: center;
  justify-content: center;
}
.share_btn a img {
  width: 100%;
  border-radius: 10px;
  object-fit: cover;
}
