.comp_service_row {
  gap: 30px;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(220px, 1fr));
}
.comp_service_card {
  box-shadow: 0 4px 8px var(--shadow-color);
  background-color: transparent;
  border-radius: 8px;
}
.comp_service_card_img {
  height: 100px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.comp_service_card_img i {
  font-size: 50px;
  color: var(--primary_color);
}
.comp_service_card_content {
  padding: 10px;
}

@media (max-width: 998px) {
  .comp_service_row {
    padding: 0 20px;
  }
}
